import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, AbstractControl } from '@angular/forms';
import { MatTableDataSource, MatSnackBar, PageEvent, MatPaginator, MatDialog } from '@angular/material';
//import { RevisaoDigitalizacao } from 'src/app/models/revisao-digitalizacao.interface';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import * as moment from 'moment';
import { EsteiraService } from '../../operation/services/esteira.service';
import { DialogComponent } from 'src/app/shared/components/dialog/dialog.component';
import { DialogTypes } from 'src/app/models/dialog-types.enum';
import { DialogPriorizarSlaComponent } from './dialog-priorizar-sla/dialog-priorizar-sla.component';
import { BrValidator } from 'src/app/shared/validators/br-validators';

@Component({
	selector: 'app-priorizar-sla',
	templateUrl: './priorizar-sla.component.html',
	styleUrls: ['./priorizar-sla.component.scss']
})

export class PriorizarSlaComponent implements OnInit {

	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public loading: boolean = false;
	public tipos: any[] = [];
	public status: any[] = [];
	public form: FormGroup;
	public statusList: string[][];
	public dataSource = new MatTableDataSource<any>();
	public displayedColumns: string[] = ['etiqueta', 'dataRecepcao', 'atividade', 'sla', 'status', 'acoes'];
	public today: Date = new Date();
	public pageSizeOptions: number[] = [5, 10, 25, 50, 100];
	public length: number = null;
	public pageSize: number = 10;
	public pageIndex: number = 0;

	constructor(
		private formBuilder: FormBuilder,
		private esteiraService: EsteiraService,
		private matSnackBar: MatSnackBar,
		public dialog: MatDialog
	) { }

	ngOnInit() {
		this.form = this.formBuilder.group({
			agencia: ['',[this.validaAgencia]],
			contaCorrente: [''],
			documento: ['',[this.validaDocumento]],
			numeroProcesso: [''],
			numeroPasta: [''],
			etiqueta: ['']
		});

		this.getResult();
	}

	public getResult(exportaXLSX: boolean = false) {
		if(this.form.valid) {
			this.loading = true;
			const form = this.form.value;
			const pageIndex = (this.pageIndex  + 1).toString();
			const pageSize = this.pageSize.toString();
			const body = {
				Agencia: form.agencia,
				ContaCorrente: form.contaCorrente,
				Documento: form.documento,
				NumeroProcesso: form.numeroProcesso,
				numeroPasta: form.numeroPasta,
				Etiqueta: form.etiqueta,
				PageSize: pageSize,
				PageIndex: pageIndex,
				ExportaXLSX: exportaXLSX
			}
			this.esteiraService.getSla(body).subscribe(
				response => {
					if (response.type == "application/octet-stream"){
						var dateToday = moment().format('YYYYMMDDHHmmss');
						var fileName = "report_" + this.form.value.atividadeId + "_" + dateToday + ".xlsx";
						var linkArquivo = window.URL.createObjectURL(response);
						var link = document.createElement("a");
						link.setAttribute("href", linkArquivo);
						link.setAttribute("download", fileName);
						link.style.display = "none";
						document.body.appendChild(link);
						link.click();
						document.body.removeChild(link);

						this.length = null;
					} else {
						this.length = response.length;
						this.dataSource = new MatTableDataSource<any>(response.data);
						this.dataSource.paginator = this.paginator;
					}
					this.loading = false;
				},
				() => {
					SnackbarComponent.showError(this.matSnackBar, null, "Erro ao obter dados. Se o erro persistir, entre em contato com o suporte.");
					this.loading = false;
				}
			);
		}
	}

	public setPageSizeOptions(event: PageEvent): void {
		this.pageIndex = event.pageIndex;
		this.pageSize = event.pageSize;
		this.getResult();
	} 

	public openModal(esteira: any){
		const dialogRef = this.dialog.open(DialogPriorizarSlaComponent, {
			width: '700px',
			height: '90%',
			data: null
		});
		dialogRef.afterClosed().subscribe(usuario => {
			if (usuario !== undefined) {
				this.priorizar(esteira.esteiraId, usuario.id);
			} else {
				SnackbarComponent.showAlert(this.matSnackBar, 'Cancelada pelo Usuário');
			}
			
		});
	}
	
	private priorizar(esteiraId: string, usuarioId: string){
		this.loading = true;
		this.esteiraService.putPriorizarEsteira(esteiraId,usuarioId).subscribe(
			() => {
				SnackbarComponent.showSuccess(this.matSnackBar, 'Priorizado com sucesso!');
				this.loading = false;
			},
			error => {
				SnackbarComponent.showError(this.matSnackBar,error);
				this.loading = false;
			}
		)
	}

	private validaDocumento(abstractControl: AbstractControl): any{
		const documento = abstractControl.value;
		if(!documento) return null;
		if(BrValidator.cpf(documento) || BrValidator.cnpj(documento)){
			return null;
		} 
	    return { isError: true };
	} 

	private validaAgencia(abstractControl: AbstractControl): any{
		const valor = abstractControl.value;
		if(!valor) return null;
		if(BrValidator.agencia(valor)){
			return null;
		} 
	    return { isError: true };
	}

}
