import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatTableDataSource, MatSnackBar } from '@angular/material';
import { AlertaAtividade } from 'src/app/models/alerta-atividade.interface';
import { AlertaService } from '../../services/alerta.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FormValidator } from 'src/app/shared/validators/form-validators';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';


@Component({
	selector: 'app-dialog-atividade',
	templateUrl: './dialog-atividade.component.html',
	styleUrls: ['./dialog-atividade.component.scss']
})
export class DialogAtividadeComponent implements OnInit {

	public displayedColumns = ['documentos', 'deletar'];
	public dataSource = new MatTableDataSource<any>([]);
	public loading: boolean = false;
	public form: FormGroup;
	public formDoc: FormGroup;
	public dataAtual: Date = new Date();
	public data: AlertaAtividade;
	public tipo: string;
	public acao: string;


	constructor(
		public dialogRef: MatDialogRef<DialogAtividadeComponent>,
		@Inject(MAT_DIALOG_DATA) public modalData: any,
		private alertaService: AlertaService,
		private formBuilder: FormBuilder,
		private matSnackBar: MatSnackBar,
	) { }

	ngOnInit() {
		this.data = this.modalData.alerta;
		this.tipo = this.modalData.tipo;
		this.acao = this.modalData.acao;

		this.form = this.formBuilder.group({
			mensagem: ['', [Validators.required, Validators.min(5), Validators.max(50)]],
			dataInicial: ['', [Validators.required]],
			dataVencimento: ['', [Validators.required]],
			observacao:  ['']
		});


		this.formDoc = this.formBuilder.group({
			documento: ['', [Validators.required, FormValidator.documento]]
		});

		if (this.data.mensagem != undefined || this.data.mensagem != null) {
			this.editar();
		}
	}

	private editar(): void {
		this.form.get('mensagem').setValue(this.data.mensagem);
		this.form.get('dataInicial').setValue(this.data.dataInicial);
		this.form.get('dataVencimento').setValue(this.data.dataVencimento);
		this.form.get('observacao').setValue(this.data.observacao);
		if (this.tipo == 'documento') {
			this.formDoc.get("documento").setValue(this.data.documento);
			//this.dataSource = new MatTableDataSource<any>([this.data.documento]);
		}
	}

	public addDocumento(): void {
		if (this.formDoc.valid) {
			//this.data.documento.push(this.formDoc.value.documento);
			this.data.documento = this.formDoc.value.documento;
			//this.dataSource = new MatTableDataSource<any>([this.data.documento]);
		}
	}

	public salvar() {
		if (this.form.valid) {
			if (this.tipo == 'atividade') {
				this.postAlerta();
			} else if (this.tipo == 'documento') {
				if (this.formDoc.valid) {
					this.postAlertaDocumentos();
				} else {
					SnackbarComponent.showError(this.matSnackBar, null, 'Adicione um documento');
				}
			}
		}
	}

	public atualizar() {
		if (this.tipo == 'atividade') {
			this.putAlerta();
		} else if (this.tipo == 'documento') {
			if (this.formDoc.valid) {
				this.putAlertaDocumentos();
			} else {
				SnackbarComponent.showError(this.matSnackBar, null, 'Adicione um documento');
			}
		}
	}

	private postAlerta() {
		this.loading = true;
		const form = this.form.value;
		const dataInicial = new Date(form.dataInicial);
		const dataVencimento = new Date(form.dataVencimento);
		const alerta = {
			dataInicial: dataInicial,
			dataVencimento: dataVencimento,
			atividadeId: this.data.atividade.id,
			tipoAlertaId: 1,
			mensagem: form.mensagem,
			observacao: form.observacao
		}
		this.alertaService.post(alerta).subscribe(
			() => {
				this.loading = false;
				this.fecharModal(this.tipo);
			},
			error => {
				this.loading = false;
				SnackbarComponent.showError(this.matSnackBar, error);
			}
		);
	}

	private putAlerta() {
		this.loading = true;
		const form = this.form.value;
		const dataInicial = new Date(form.dataInicial);
		const dataVencimento = new Date(form.dataVencimento);
		const alerta = {
			id: this.data.id,
			dataInicial: dataInicial,
			dataVencimento: dataVencimento,
			atividadeId: this.data.atividade.id,
			mensagem: form.mensagem,
			observacao: form.observacao,
			tipoAlertaId: this.data.tipoAlertaId
		}
		this.alertaService.put(alerta).subscribe(
			() => {
				this.loading = false;
				this.fecharModal(this.tipo);
			},
			error => {
				this.loading = false;
				SnackbarComponent.showError(this.matSnackBar, error);
			}
		);
	}

	private postAlertaDocumentos() {
		this.loading = true;
		const form = this.form.value;
		const dataInicial = new Date(form.dataInicial);
		const dataVencimento = new Date(form.dataVencimento);
		const alerta = {
			dataInicial: dataInicial,
			dataVencimento: dataVencimento,
			atividadeId: this.data.atividade.id,
			tipoAlertaId: 1,
			mensagem: form.mensagem,
			observacao: form.observacao,
			documento: this.formDoc.value.documento
		}
		this.alertaService.postDocumentos(alerta).subscribe(
			() => {
				this.loading = false;
				this.fecharModal(this.tipo);
			},
			error => {
				this.loading = false;
				SnackbarComponent.showError(this.matSnackBar, error);
			}
		);
	}

	private putAlertaDocumentos() {
		this.loading = true;
		const form = this.form.value;
		const dataInicial = new Date(form.dataInicial);
		const dataVencimento = new Date(form.dataVencimento);
		const alerta = {
			id: this.data.id,
			dataInicial: dataInicial,
			dataVencimento: dataVencimento,
			atividadeId: this.data.atividade.id,
			mensagem: form.mensagem,
			observacao: form.observacao,
			tipoAlertaId: this.data.tipoAlertaId,
			documento: this.formDoc.value.documento
		}
		this.alertaService.putDocumentos(alerta).subscribe(
			() => {
				this.loading = false;
				this.fecharModal(this.tipo);
			},
			error => {
				this.loading = false;
				SnackbarComponent.showError(this.matSnackBar, error);
			}
		);
	}

	public fecharModal(status?: string) {
		this.dialogRef.close(status);
	}

	public deletar(documento: any) {

		//this.data.documento = this.data.documento.filter(element => element != documento);
		this.data.documento = null;
		this.dataSource.data = [];
	}
}
