import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { ResponseList } from '../../../models/response-list.interface';
import { ResponseObject } from 'src/app/models/response-object.interface';
import { Usuario } from 'src/app/models/usuario.interface';
import { UsuarioFila } from 'src/app/models/usuario-fila.interface';



@Injectable({
	providedIn: 'root'
})
export class MonitorUsersService {
	private urlUsuarioPushService: string = '/api/v1/usuarios';
	private urlPerfilPushService: string = '/api/v1/perfis';

	constructor (
		private http: HttpClient,
	) { }
 
	public find(
		nome: string, 
		pageIndex: string, 
		pageSize: string): Observable<ResponseList> {
		return this.http.get<ResponseList>(environment.urlServices + this.urlUsuarioPushService, {
			params : {
				Nome : nome,
				PageIndex : pageIndex,
				PageSize : pageSize
			}
		});
	} 

	public getPerfis(): Observable<ResponseObject> {
		return this.http.get<ResponseObject>(environment.urlServices + this.urlPerfilPushService);
	} 

	public getFilas(userId: string): Observable<ResponseObject> {
		return this.http.get<ResponseObject>(environment.urlServices + this.urlUsuarioPushService + '/' + userId + '/filas');
	} 

	public get(userId: string): Observable<ResponseObject> {
		return this.http.get<ResponseObject>(environment.urlServices + this.urlUsuarioPushService + '/' + userId);
	} 

	public put(formulario: Usuario, userId: string): Observable<ResponseObject> {
		const data = {
			id : userId,
			statusId : formulario.statusId,
			perfilId : formulario.perfilId,
			email: formulario.email,
			bus: formulario.bus
		};
		return this.http.put<ResponseObject>(environment.urlServices + this.urlUsuarioPushService, data);
	} 
	
	public postFilas(formulario: UsuarioFila[], userId: string): Observable<ResponseObject> {
		const data = {
			filas : formulario
		};		
		return this.http.post<ResponseObject>(environment.urlServices + this.urlUsuarioPushService + '/' + userId + '/filas', data);
	}

	public getUsuarios(	pageIndex: string, pageSize: string): Observable<ResponseList> {
		return this.http.get<ResponseList>(environment.urlServices + this.urlUsuarioPushService, {
			params : {
				PageIndex : pageIndex,
				PageSize : pageSize
			}
		});
	} 
}
