import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { OperationService } from 'src/app/services/operation.service';
import { Operacao } from 'src/app/models/operacao.interface';
import { Atividade } from 'src/app/models/atividade.interface';
import { AlertaAtividade } from 'src/app/models/alerta-atividade.interface';
import { MatTableDataSource, MatSnackBar, MatDialog, MatDialogRef } from '@angular/material';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import * as moment from 'moment';
import { AlertaService } from '../services/alerta.service';
import { DialogAtividadeComponent } from './dialog-atividade/dialog-atividade.component';
import { Dialog } from 'src/app/models/dialog.model';
import { DialogComponent } from 'src/app/shared/components/dialog/dialog.component';
import { DialogTypes } from 'src/app/models/dialog-types.enum';


@Component({
	selector: 'app-alerta-atividade',
	templateUrl: './alerta-atividade.component.html',
	styleUrls: ['./alerta-atividade.component.scss']
})

export class AlertaAtividadeComponent implements OnInit {

	//@ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
	public loading: boolean = false;
	public operations: Operacao[] = [];
	public activities: Atividade[] = [];
	public form: FormGroup;
	public displayedColumns: string[] = ['atividade', 'mensagem', 'dataInicial', 'dataVencimento', 'editar', 'deletar'];
	public dataSource = new MatTableDataSource<AlertaAtividade>();
	public displayedColumnsDocumentos: string[] = ['atividade', 'mensagem', 'dataInicial', 'dataVencimento', 'documentos', 'editar', 'deletar'];
	public dataSourceDocumentos = new MatTableDataSource<AlertaAtividade>();
	public alertasAtividades: AlertaAtividade[] = [];
	public alertasAtividadesDocumentos: AlertaAtividade[] = [];
	public tipo = 'atividade';
	public dialogReference: MatDialogRef<DialogComponent, any>;

	constructor(
		private formBuilder: FormBuilder,
		private operationService: OperationService,
		private alertaService: AlertaService,
		private matSnackBar: MatSnackBar,
		public dialog: MatDialog
	) { }
		
	ngOnInit() {
		this.form = this.formBuilder.group({
			operacaoId: ['', Validators.required],
			atividade: ['', Validators.required]
		});
		this.operacoes();
		this.getAlertas();
		this.getAlertasDocumentos();
	}
		
	private operacoes() {
		this.loading = true;
		this.operationService.getOperations(true).subscribe(
			response => {
				this.operations = [...response.data];
				this.loading = false;
			},
			() => {
				SnackbarComponent.showError(this.matSnackBar, null, "Erro ao obter a lista de Operações e Atividades. Se o erro persistir, entre em contato com o suporte.");
				this.loading = false;
			}
		);
	}
		
	public setOperation(): void {
		const operation: Operacao = this.operations.find(el => el.operacaoId === this.form.value.operacaoId);
		this.activities = [...operation.atividades];
		//this.form.get('atividade').enable();
	}

	private getAlertas() {
		this.loading = true;
		this.alertaService.get().subscribe(
			response => {
				this.alertasAtividades = [...response.data];
				this.dataSource.data = this.alertasAtividades;
				this.loading = false;
		
			},
			() => {
				SnackbarComponent.showError(this.matSnackBar, null, "Erro ao obter a lista de Alertas");
				this.loading = false;
			}
		);
	}

	private getAlertasDocumentos() {
		this.loading = true;
		this.alertaService.getDocumentos().subscribe(
			response => {
				this.alertasAtividadesDocumentos = [...response.data];
				this.dataSourceDocumentos.data = this.alertasAtividadesDocumentos;
				this.loading = false;
		
			},
			() => {
				SnackbarComponent.showError(this.matSnackBar, null, "Erro ao obter a lista de Alertas");
				this.loading = false;
			}
		);
	}
	

	public openModal(alertaAtividade: AlertaAtividade, acao: string): void {
		const dialogRef = this.dialog.open(DialogAtividadeComponent, {
			width: '80%',
			maxHeight: '100%',
			height: '90%',
			data: {alerta: alertaAtividade, tipo: this.tipo, acao: acao}
		});
		dialogRef.afterClosed().subscribe(result => {
			if (result == undefined) {
				SnackbarComponent.showAlert(this.matSnackBar, "Cancelada pelo usuário!");
			} else if (result == 'atividade') {
				SnackbarComponent.showSuccess(this.matSnackBar, "Alerta cadastrado com Sucesso!");
				this.getAlertas();
			} else if (result == 'documento'){
				SnackbarComponent.showSuccess(this.matSnackBar, "Alerta Documentos cadastrado com Sucesso!");
				this.getAlertasDocumentos();
			}
		});
	}


	public add() {
		if(this.form.valid) {
			let check = false;
			if(this.tipo == 'atividade') {
				check = (this.alertasAtividades.findIndex(element => element.atividade.id == this.form.value.atividade.id) < 0);
			} else if(this.tipo == 'documento') {
				//check = (this.alertasAtividadesDocumentos.findIndex(element => element.atividade.id == this.form.value.atividade.id) < 0);
				check = true;
			}
			if(check) {
				let alerta = {  dataInicial: null, dataVencimento: null, mensagem: null, documento: null , atividade: { id: this.form.value.atividade.id, nome: this.form.value.atividade.nome }};
				this.openModal(alerta, 'new')

			} else {
				SnackbarComponent.showAlert(this.matSnackBar, "O alerta para essa atividade já foi cadastrado", null );
			}
		}
	}

	public edit(alertaAtividade: AlertaAtividade) {
		this.openModal(alertaAtividade, 'edit');
	}

	public deletarAlerta(id: any): void {
		let dialog: Dialog = new Dialog();
		dialog.title = "Confirmar Exclusão";
		dialog.message = "Você tem certeza que deseja excluir o Alerta adicionado?";
		dialog.type = DialogTypes.CONFIRMATION;
		this.showMessage(dialog);

		this.dialogReference.afterClosed().subscribe(
			response => {
				if (response === 'ok') {
					this.alertaService.delete(id).subscribe(
						() => {
							this.getAlertas();
						},
						error => {
							SnackbarComponent.showError(this.matSnackBar, error);
						}
					);
				}
			}
		);
	}

	public deletarDocumento(id: any): void {
		let dialog: Dialog = new Dialog();
		dialog.title = "Confirmar Exclusão";
		dialog.message = "Você tem certeza que deseja excluir o Alerta adicionado?";
		dialog.type = DialogTypes.CONFIRMATION;
		this.showMessage(dialog);

		this.dialogReference.afterClosed().subscribe(
			response => {
				if (response === 'ok') {
					this.alertaService.deleteDocumento(id).subscribe(
						() => {
							this.getAlertasDocumentos();
						},
						error => {
							SnackbarComponent.showError(this.matSnackBar, error);
						}
					);
				}
			}
		);
	}

	public showMessage(dialog: Dialog): void {
		this.dialogReference = this.dialog.open(DialogComponent, {
			width: '600px',
			data: {
				title: dialog.title,
				message: dialog.message,
				type: dialog.type,
			}
		});
	}
	
}