import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AdministratorRoutingModule } from './administrator-routing.module';
import { SharedModule } from 'src/app/shared/shared.module';
import { 
	MatFormFieldModule, 
	MatInputModule,
	MatPaginatorModule,
	MatTableModule,
	MatSelectModule,
	MatTreeModule,
	MatIconModule,
	MatDividerModule,
	MatRadioModule,
	MatListModule,
	MatCardModule,
	MatCheckboxModule,
	MatGridListModule,
	MatProgressSpinnerModule,
	MatSlideToggleModule,
	MatButtonModule,
	DateAdapter,
	MAT_DATE_FORMATS,
	MatDatepickerModule,
	MatMenuModule,
	MatTabsModule,
	MatTooltipModule,
	MatExpansionModule,
	MatProgressBarModule
} from '@angular/material';
import { ReactiveFormsModule } from '@angular/forms';
import { MonitorUsersComponent } from './monitor-users/monitor-users.component';
import { MonitorUsersService } from './services/monitor-users.service';
import { NgxMaskModule } from 'ngx-mask';
import { MonitorUsersPerfilComponent } from './monitor-users/monitor-users-perfil/monitor-users-perfil.component';
import { MonitorUsersFilasComponent } from './monitor-users/monitor-users-filas/monitor-users-filas.component';
import { DialogUsersFilasomponent } from './monitor-users/monitor-users-filas/dialog-users-filas/dialog-users-filas.component';
import { DragDropModule } from '@angular/cdk/drag-drop';
import { CustomDateAdapter } from 'src/app/shared/dateAdapter/customDate.adapter';
import { MY_DATE_FORMATS } from 'src/app/shared/dateAdapter/my-date-formats.const';
import { AlertaAtividadeComponent } from './alerta-atividade/alerta-atividade.component';
import { AlertaService } from './services/alerta.service';
import { DialogAtividadeComponent } from './alerta-atividade/dialog-atividade/dialog-atividade.component';
import { PriorizarSlaComponent } from './priorizar-sla/priorizar-sla.component';
import { DialogPriorizarSlaComponent } from './priorizar-sla/dialog-priorizar-sla/dialog-priorizar-sla.component';

@NgModule({
	declarations: [
		MonitorUsersComponent, 
		MonitorUsersPerfilComponent,
		MonitorUsersFilasComponent,
		DialogUsersFilasomponent,
		AlertaAtividadeComponent,
		DialogAtividadeComponent,
		PriorizarSlaComponent,
		DialogPriorizarSlaComponent
	],
	imports: [
		CommonModule,
		SharedModule,
		MatCardModule,
		MatCheckboxModule,
		MatDividerModule,
		MatFormFieldModule,
		MatGridListModule,
		MatInputModule,
		MatListModule,
		MatPaginatorModule,
		MatProgressSpinnerModule,
		MatSlideToggleModule,
		MatButtonModule,
		MatRadioModule,
		MatSelectModule,
		MatTableModule,
		ReactiveFormsModule,
		AdministratorRoutingModule,
		NgxMaskModule.forRoot(),
		MatTreeModule,
		MatIconModule,
		DragDropModule,
		MatDatepickerModule,
		MatMenuModule,
		MatTabsModule,
		MatTooltipModule,
		MatExpansionModule,
		MatProgressBarModule
	],
	exports: [
		
	],
	providers: [
		MonitorUsersService,
		AlertaService,
		{ provide: DateAdapter, useClass: CustomDateAdapter },
		{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }
	],
	entryComponents : [
		DialogUsersFilasomponent,
		DialogAtividadeComponent,
		DialogPriorizarSlaComponent
	]
})
export class AdministratorModule { }
