import { Component, OnInit, Inject, Injectable} from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatTreeFlatDataSource, MatTreeFlattener } from '@angular/material';
import { FlatTreeControl } from '@angular/cdk/tree';
import { TreeFlatNode } from 'src/app/models/tree-flat-node.model';
import { BehaviorSubject } from 'rxjs';
import { Tree } from 'src/app/models/tree.model';
import { SelectionModel } from '@angular/cdk/collections';

@Component({
	selector: 'dialog-users-filas',
	templateUrl: './dialog-users-filas.component.html',
	styleUrls: ['./dialog-users-filas.component.scss']
})

@Injectable()
export class DialogUsersFilasomponent implements OnInit {

	public dataChange = new BehaviorSubject<Tree[]>([]);
	get dados(): Tree[] { return this.dataChange.value; }

	private _transformer = (node: Tree, level: number) => {
		return {
			expandable: !!node.children && node.children.length > 0,
			name: node.name,
			level: level,
			checked: node.checked,
			id: node.id,
			prioridade: node.prioridade,
			fila: node.fila,
			filaId: node.filaId,
			atividadeId: node.atividadeId,
			priorizado: node.priorizado
		};
	}

	public treeControl = new FlatTreeControl<TreeFlatNode>(node => node.level, node => node.expandable);
	public treeFlattener = new MatTreeFlattener(this._transformer, node => node.level, node => node.expandable, node => node.children);
	public dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
	public checklistSelection = new SelectionModel<TreeFlatNode>(true /* multiple */);

	constructor(
		public dialogRef: MatDialogRef<DialogUsersFilasomponent>,
		@Inject(MAT_DIALOG_DATA) public data: any
	) { 
		this.dataSource.data = data;
		
	}
	
	public hasChild = (_: number, node: TreeFlatNode) => node.expandable;
	public getLevel = (node: TreeFlatNode) => node.level;
	public hasNoContent = (_: number, _nodeData: TreeFlatNode) => _nodeData.item === '';

	ngOnInit() {
		
	}

	public todoItemSelectionToggle(node: TreeFlatNode, event: any): void {
		this.checklistSelection.toggle(node);
		const descendants = this.treeControl.getDescendants(node);
		this.checklistSelection.isSelected(node) ? this.checklistSelection.select(...descendants) : this.checklistSelection.deselect(...descendants);
		// Force update for the parent
		descendants.forEach(child =>
		  	this.checklistSelection.isSelected(child)
		);
		this.checkAllParentsSelection(node);
		this.atualizaGrupo(event, node);
	}

	public checkAllParentsSelection(node: TreeFlatNode): void {
		let parent: TreeFlatNode | null = this.getParentNode(node);
		while (parent) {
			this.checkRootNodeSelection(parent);
			parent = this.getParentNode(parent);
		}
	}

	/* Get the parent node of a node */
	public getParentNode(node: TreeFlatNode): TreeFlatNode | null {
		const currentLevel = this.getLevel(node);
		if (currentLevel < 1) {
		 	return null;
		}
		const startIndex = this.treeControl.dataNodes.indexOf(node) - 1;
	
		for (let i = startIndex; i >= 0; i--) {
			const currentNode = this.treeControl.dataNodes[i];
			if (this.getLevel(currentNode) < currentLevel) {
				return currentNode;
			}
		}
		return null;
	}

	/** Whether all the descendants of the node are selected. */
	public descendantsAllSelected(node: TreeFlatNode): boolean {
		const descendants = this.treeControl.getDescendants(node);
		const descAllSelected = descendants.every(child =>
		  	this.checklistSelection.isSelected(child)
		);
		return descAllSelected;
	}
	  /** Check root node checked state and change it accordingly */
	public checkRootNodeSelection(node: TreeFlatNode): void {
		const nodeSelected = this.checklistSelection.isSelected(node);
		const descendants = this.treeControl.getDescendants(node);
		const descAllSelected = descendants.every(child =>
		this.checklistSelection.isSelected(child)
		);
		if (nodeSelected && !descAllSelected) {
		this.checklistSelection.deselect(node);
		} else if (!nodeSelected && descAllSelected) {
		this.checklistSelection.select(node);
		}
	}

	/** Whether part of the descendants are selected */
	public descendantsPartiallySelected(node: TreeFlatNode): boolean {
		const descendants = this.treeControl.getDescendants(node);
		const result = descendants.some(child => this.checklistSelection.isSelected(child));
		return result && !this.descendantsAllSelected(node);
	}
	/** Toggle a leaf to-do item selection. Check all the parents to see if they changed */
	public todoLeafItemSelectionToggle(node: TreeFlatNode, event: any): void {
		this.checklistSelection.toggle(node);
		this.checkAllParentsSelection(node);
	}

	public atualizar(){
		this.dialogRef.close(this.treeControl);
	}

	public cancel() {
		this.dialogRef.close();
	}

	public atualizaGrupo(event: any, node: TreeFlatNode) {
		if (node.level == 0) {
			this.dataSource.data.forEach(item =>{
				if(item.id == node.id) {
					item.children.forEach(i=>{
						i.checked = event.checked;
					})
				}
			});
		} else if (node.level == 1){
			this.dataSource.data.forEach(item =>{
				item.children.forEach(it=>{
					if(it.id == node.id) {
						it.children.forEach(i=>{
							i.checked = event.checked;
						})
					}
				})
			});
		}
		
		//quando submeter
		this.data = this.dataSource.data;
		// Notify the change.
		this.dataChange.next(this.data);
	}

	public atualizaNivel2(event: any, node: TreeFlatNode) {
		if (node.level == 1) {
			this.dataSource.data.forEach(a => {
				if(a.id == node.id) {
					a.children.forEach(i=>{
						i.checked = event.checked;
					})
				}
			});
		}
		this.dataChange.next(this.dados);
	}

	public atualizaDataLevel(){

	}

	buildFileTree(obj: {[key: string]: any}, level: number): Tree[] {
		return Object.keys(obj).reduce<Tree[]>((accumulator, key) => {
			const value = obj[key];
			const node = new Tree();
			node.item = key;
			
			if (value != null) {
				if (typeof value === 'object') {
				node.children = this.buildFileTree(value, level + 1);
				} else {
				node.item = value;
				}
			}
			
			return accumulator.concat(node);
		}, []);
	}
}
