import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Usuario } from 'src/app/models/usuario.interface';
import { MonitorUsersService } from '../../services/monitor-users.service';
import { ActivatedRoute, Router } from '@angular/router';
import { TypeDocumentsToArchive } from 'src/app/models/type-documents-archive.interface';
import { Perfil } from 'src/app/models/perfil.interface';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { MatSnackBar } from '@angular/material';
import { Bus } from 'src/app/models/bus.interface';

@Component({
	selector: 'app-monitor-users-perfil',
	templateUrl: './monitor-users-perfil.component.html',
	styleUrls: ['./monitor-users-perfil.component.scss']
})
export class MonitorUsersPerfilComponent implements OnInit {
	public formEntity: FormGroup;
	public userId: string;
	public user: Usuario;
	public tipoPerfis: Perfil[] = [];
	public tipoStatus: TypeDocumentsToArchive[] = [
		{ id: 1, label: 'Pendente', nome: null },
		{ id: 2, label: 'Ativo', nome: null },
		{ id: 3, label: 'Inativo', nome: null }
	];

	private busIds: number[] = [];

	public bus: Bus[] = [];
	public loader: boolean = false;

	constructor(
		private activatedRoute: ActivatedRoute,
		private formBuilder: FormBuilder,
		private userService: MonitorUsersService,
		private router: Router,
		private matSnackBar: MatSnackBar
	) {}

	ngOnInit() {
		this.loader = true;
		this.userId = this.activatedRoute.snapshot.paramMap.get('id');
		this.formEntity = this.formBuilder.group({
			statusId: [0, Validators.required],
			perfilId: ['', Validators.required],
			email: ['', Validators.required],
			bus: [],
		});

		this.userService.getPerfis().subscribe(
			response => {
				this.tipoPerfis = response.data;
				this.get();
			},
			error => {
				SnackbarComponent.showError(this.matSnackBar, error);
				this.loader = false;
			}
		);

		
	}

	public get(): void {
		this.userService.get(this.userId).subscribe(
			response => {
				this.user = response.data;
				this.formEntity.controls['statusId'].setValue(this.user.statusId);
				this.formEntity.controls['perfilId'].setValue(this.user.perfilId);
				this.formEntity.controls['email'].setValue(this.user.email);
				this.loader = false;
				let busIds = [];
				if(this.user.bus != undefined){
					 this.user.bus.forEach(item=>{
						if(item.checked == true){
							busIds.push(item.id);
						}
					})
					this.formEntity.get("bus").setValue(busIds);
				}
			},
			error => {
				SnackbarComponent.showError(this.matSnackBar, error);
				this.loader = false;
			}
		);
	}

	public put(): void {
		this.loader = true;
		if (this.formEntity.valid) {
			this.userService.put(this.formEntity.value, this.userId).subscribe(
				() => {
					SnackbarComponent.showSuccess(this.matSnackBar);
					this.router.navigate(['/administracao/usuarios']);
					this.loader = false;
				}, error => {
					SnackbarComponent.showError(this.matSnackBar, error);
					this.loader = false;
				}
			);
		}
	}

}



