import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { ResponseObject } from 'src/app/models/response-object.interface';

@Injectable({
	providedIn: 'root'
})
export class AlertaService {
	private urlAlerta: string = '/api/v1/alertasAtividade';
	private urlDocumento: string = '/api/v1/alertasDocumento';

	constructor(
		private http: HttpClient,
	) { }

	public get(): Observable<ResponseObject> {
		return this.http.get<ResponseObject>(environment.urlServices + this.urlAlerta);
	}

	public post(data: any): Observable<ResponseObject> {
		return this.http.post<ResponseObject>(environment.urlServices + this.urlAlerta, data);
	}

	public put(data: any): Observable<ResponseObject> {
		return this.http.put<ResponseObject>(environment.urlServices + this.urlAlerta, data);
	}

	public delete(id: any): Observable<ResponseObject> {
		return this.http.delete<ResponseObject>(`${environment.urlServices}${this.urlAlerta}/${id}`);
	}

	public getDocumentos(): Observable<ResponseObject> {
		return this.http.get<ResponseObject>(environment.urlServices + this.urlDocumento);
	}

	public postDocumentos(data: any): Observable<ResponseObject> {
		return this.http.post<ResponseObject>(environment.urlServices + this.urlDocumento, data);
	}

	public putDocumentos(data: any): Observable<ResponseObject> {
		return this.http.put<ResponseObject>(environment.urlServices + this.urlDocumento, data);
	}

	public deleteDocumento(id: any): Observable<ResponseObject> {
		return this.http.delete<ResponseObject>(`${environment.urlServices}${this.urlDocumento}/${id}`);
	}
}
