import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MonitorUsersComponent } from './monitor-users/monitor-users.component';
import { MonitorUsersPerfilComponent } from './monitor-users/monitor-users-perfil/monitor-users-perfil.component';
import { MonitorUsersFilasComponent } from './monitor-users/monitor-users-filas/monitor-users-filas.component';
import { AdministratorRoutesGuard } from './administrator-routes.guard';
import { AlertaAtividadeComponent } from './alerta-atividade/alerta-atividade.component';
import { PriorizarSlaComponent } from './priorizar-sla/priorizar-sla.component';

const routes: Routes = [
	{ path: 'usuarios', component: MonitorUsersComponent, canActivate: [ AdministratorRoutesGuard ] },
	{ path: 'usuarios/:id/perfil', component: MonitorUsersPerfilComponent, canActivate: [ AdministratorRoutesGuard ] },
	{ path: 'usuarios/:id/filas', component: MonitorUsersFilasComponent, canActivate: [ AdministratorRoutesGuard ] },
	{ path: 'priorizar-sla', component: PriorizarSlaComponent, canActivate: [ AdministratorRoutesGuard ] },
	{ path: 'alerta-atividade', component: AlertaAtividadeComponent, canActivate: [ AdministratorRoutesGuard ] }
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class AdministratorRoutingModule { }
