import { Component, OnInit, Inject} from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatPaginator, MatSnackBar, MatTableDataSource, PageEvent  } from '@angular/material';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { MonitorUsersService } from '../../services/monitor-users.service';


@Component({
	selector: 'dialog-priorizar-sla',
	templateUrl: './dialog-priorizar-sla.component.html',
	styleUrls: ['./dialog-priorizar-sla.component.scss']
})

export class DialogPriorizarSlaComponent implements OnInit {

	public usuarios: any = [];
	public loading: boolean = false;
	public dataSource = new MatTableDataSource<any[]>([]);
	public displayedColumns: string[] = ['usuario', 'actions'];
	public dataAtual: Date = new Date();
	public length: number = null;
	public pageSize: number = 100;
	public pageIndex: number = 0;
	public pageSizeOptions: number[] = [5, 10, 25, 50, 100];

	constructor(
		public dialogRef: MatDialogRef<DialogPriorizarSlaComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private userService: MonitorUsersService,
		private matSnackBar: MatSnackBar,
	) { }
	
	ngOnInit() {
		this.getUsuarios();
	}

	public getUsuarios() {
		this.loading = true;
		const pageIndex = (this.pageIndex  + 1).toString();
		const pageSize = this.pageSize.toString();
		this.userService.getUsuarios(pageIndex, pageSize).subscribe(
			response => {
				this.usuarios = response.data;
				this.length = response.length;
				this.dataSource.data = this.usuarios;
				this.loading = false;
			},
			error => {
				SnackbarComponent.showError(this.matSnackBar, error);
				this.loading = false;
			}
		);
	}

	public setPageSizeOptions(eventOptions: PageEvent) {
		this.pageIndex = eventOptions.pageIndex;
		this.pageSize = eventOptions.pageSize;
		this.getUsuarios();
	}

	public cancel() {
		this.dialogRef.close();
	}

	public selecionar(usuario: any) {
		this.dialogRef.close(usuario);
	}
}
