import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatTableDataSource, MatPaginator, MatSnackBar } from '@angular/material';
import { tap, debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { PageEvent } from '@angular/material/paginator';
import { Usuario } from 'src/app/models/usuario.interface';
import { MonitorUsersService } from '../services/monitor-users.service';
import { Subject } from 'rxjs';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';

@Component({
	selector: 'app-monitor-users',
	templateUrl: './monitor-users.component.html',
	styleUrls: ['./monitor-users.component.scss']
})
export class MonitorUsersComponent implements OnInit, AfterViewInit {

	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public formFilter: FormGroup;
	public displayedColumns: string[] = ['login', 'lacre', 'perfil', 'status', 'actions'];
	public dataSource = new MatTableDataSource<Usuario>();
	public users: Usuario[] = [];
	public length: number;
	public pageSize = 5;
	public pageIndex = 0;
	public pageSizeOptions: number[] = [5, 10, 25, 50, 100];
	public filterDebounce = new Subject<string>();
	public loader: boolean = false;

	constructor(
		private formBuilder: FormBuilder,
		private envelopeService: MonitorUsersService,
		private matSnackBar: MatSnackBar
	) { 

		this.filterDebounce.pipe(
			debounceTime(500),
			distinctUntilChanged())
			.subscribe(value => {
				this.pageIndex = 0;
				this.pageSize = 5;
				const valueLength : number = value.trim().length;
				if (valueLength > 3 || valueLength === 0) {
					this.find();
				}				
			});

	}
	
	ngOnInit() {
		this.formFilter = this.formBuilder.group({
			nome : ['']
		});

		this.find();
	}

	ngAfterViewInit() {
		if (this.paginator) {
			this.paginator.page.pipe(
				tap(() => this.find())
			).subscribe();
		}
    }

	public find(): void {
		this.loader = true;
		this.users = [];
		const pageIndex = (this.pageIndex  + 1).toString();
		const pageSize = this.pageSize.toString();
		this.envelopeService.find(this.formFilter.value.nome, pageIndex, pageSize).subscribe(
			response => {
				this.loader = false;
				this.users = [...response.data]
				this.dataSource = new MatTableDataSource<Usuario>(this.users);
				this.length = response.length;
			},
			error => {
				SnackbarComponent.showError(this.matSnackBar, error);
				this.loader = false;
			}
		);
	}

	public setPageSizeOptions(eventOptions: PageEvent) {
		this.pageIndex = eventOptions.pageIndex;
		this.pageSize = eventOptions.pageSize;
		this.find();
	}
}

	

